import { ControlValueAccessor } from '@angular/forms';
import { BooleanInput, coerceBooleanProperty } from '@angular/cdk/coercion';

export class BaseControlValueAccessor<T> implements ControlValueAccessor {

  private _val!: T | null;
  private _disabled: boolean = false;
  private _updateOn: 'change' | 'blur' | 'submit' | undefined;

  get val(): T | null {
    return this._val;
  }

  set val(value: T | null) {
    if (this._val !== value) {
      this._val = value;

      if(this.updateOn === 'change') {
        this.onChange(value);
        this.onTouched();
      }
    }
  }

  get disabled(): boolean {
    return this._disabled;
  }

  set disabled(value: BooleanInput) {
    this._disabled = coerceBooleanProperty(value);
  }

  get updateOn(): 'change' | 'blur' | 'submit' {
    return this._updateOn ?? 'change';
  }

  set updateOn(value: 'change' | 'blur' | 'submit' | undefined) {
    this._updateOn = value;
  }

  onChange = (_: any) => {};

  onTouched = () => {};

  writeValue(value: T): void {
    this.val = value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(value: boolean): void {
    this.disabled = value;
  }

  emitValueOn(): void {
    this.onChange(this._val);
    this.onTouched();
  }
}
