import { Component, EventEmitter, inject, Input, LOCALE_ID, Output } from '@angular/core';
import { FormGroupDirective, NgControl, NgForm } from '@angular/forms';
import { registerLocaleData } from '@angular/common';

import localePt from '@angular/common/locales/pt';

import { FieldErrorMessengerService } from './services/field-error-messenger.service';
import { BaseControlValueAccessor } from './base-control-value-accessor';
import { CustomErrorStateMatcher } from './custom-error-state-matcher';

registerLocaleData(localePt, 'pt');

type Size = 'large' | 'standard' | 'small';

type Type = 'text' | 'password' | 'number';

@Component({
  template: '',
  standalone: true,
  providers: [{ provide: LOCALE_ID, useValue: 'pt' }]
})
export class BaseComponent<T> extends BaseControlValueAccessor<T> {
  protected errorMessageService = inject(FieldErrorMessengerService);

  @Output() blur: EventEmitter<any> = new EventEmitter<any>();

  @Input() label?: string;

  @Input() labelHint?: string;

  @Input() labelHintTooltip?: string;

  @Input() name!: string;

  @Input() placeholder!: string;

  @Input() formFieldHint = '';

  @Input() errorMessage?: string;

  @Input() showIconPassword: boolean = true;

  @Input() readOnly: boolean = false;

  @Input('aria-describedby') ariaDescribedby!: string

  @Input('aria-label') ariaLabel!: string

  @Input('aria-labelledby') ariaLabelledby!: string | null

  @Input()
  get type(): Type {
    return this._type;
  }

  set type(value: Type) {
    this.isPasswordField = value === 'password';
    this._type = value;
  }

  private _type: Type = 'text';

  protected isPasswordField = false;

  @Input()
  get size(): Size {
    return this._size;
  }

  set size(value: Size) {
    this._size = value;
    this.formFieldLabelSize = `mat-label--${value}`;
    this.formFieldSize = `mat-mdc-form-field--${value}`;
    this.selectOptionSize = `mat-mdc-select-panel--${value}`;
    this.selectOption = `mat-mdc-option--${value}`;
  }

  private _size: Size = 'standard';

  protected formFieldLabelSize = 'mat-label--standard';

  protected formFieldSize = 'mat-mdc-form-field--standard';

  protected selectOptionSize = 'mat-mdc-select-panel--standard';

  protected selectOption = 'mat-mdc-option--standard';

  @Input()
  get maxlength(): number {
    return this._maxlength;
  }
  set maxlength(value: number | string) {
    const length = typeof value === 'string' ? parseInt(value) : value;
    if (!Number.isNaN(length) && Number.isInteger(length)) this._maxlength = length;
  }

  private _maxlength = 45;

  constructor(private _ngControl: NgControl, private _parentForm: NgForm, private _parentFormGroup: FormGroupDirective) {
    super();
  }

  protected matcher = new CustomErrorStateMatcher(this._ngControl, this._parentForm, this._parentFormGroup);

  get errorText(): string {
    return this.errorMessageService.getErrorMessage(this._ngControl, this.errorMessage);
  }
}
